<script>
import { errorMessage } from '../utils/stores.js';
import { LABELS } from '../utils/constant.js';
import ProductItem from './ProductItem.svelte';

export let type;
export let results;

function getCategoryName(label) {
  if (typeof label === 'string' && label.indexOf('.') > -1) {
    return LABELS[label]['en-US'];
  }
  return LABELS[LABELS[label]]['en-US'];
}

function apiError(err) {
  errorMessage.set(err);
  return '';
}

function monthDistance(a, b) {
  let am = new Date(a).getMonth();
  let bm = new Date(b).getMonth();
  let d = Math.abs(am - bm);
  if (d > 6) {
    d = 11 - d;
  }
  return d;
}
</script>

{#await results}
  <div id="result-list" class="loading"></div>
{:then json}
  <div id="result-list">
    {#if type === 'recommend' && json !== undefined && Object.keys(json).length > 0}
      {#each Object.keys(json) as label}
        <h2>{getCategoryName(label)}</h2>
        <div class="grid-list">
          {#each json[label].sort((a, b) => {
              let s = '2020-10-27 11:21:51';
              let c = monthDistance(a.regdate, s);
              let d = monthDistance(b.regdate, s);
              return c - d;
            }) as product}
            <ProductItem product={product} />
          {/each}
        </div>
      {/each}
    {:else if type === 'recommend-multi' && json !== undefined && json.length > 0}
      {#each json as set, i}
        <h2>set {i + 1}</h2>
        <div class="grid-list">
          {#each set as product}
            <ProductItem product={product} />
          {/each}
        </div>
      {/each}
    {:else if type === 'search' && json !== undefined && json.length > 0}
      <div class="grid-list">
        {#each json.sort((a, b) => {
          let s = json[0].regdate;
          let c = monthDistance(a.regdate, s);
          let d = monthDistance(b.regdate, s);
          // console.log(a, b, s, c, d)
          return c - d;
        }) as product}
          <ProductItem product={product} />
        {/each}
      </div>
    {:else if json !== undefined}
      No results found.
    {/if}
  </div>
{:catch err}
  {apiError(err)}
{/await}
