<script>
import { LABELS, CATEGORY } from '../utils/constant.js';

export let image_url;
export let regions;
export let analyze;
export let current_region;
export let change_region;

function get_element_id(region) {
  let category = region.category1 ? region.category1.label : region.category.code;
  var elem_id = "detection_" + category + "_" + region.id;
  elem_id = elem_id.replace(/,/gi, "_");
  return elem_id;
}

function getCategoryName(region) {
  if (region.category1 && LABELS[region.category1.label]) {
    return LABELS[region.category1.label]['en-US'];
  }
  else if (region.category && LABELS[region.category.code] &&
      ['c1', 'c2', 'c3'].indexOf(LABELS[region.category.code].split('.')[0]) > -1 &&
      LABELS[LABELS[region.category.code]]) {
    return LABELS[LABELS[region.category.code]]['en-US'];
  }
  else if (CATEGORY[region.category.code.toString()]) {
    return CATEGORY[region.category.code.toString()].str;
  }
  return '';
}

function getCurrentAttributes(currentRegion, analyzeResult) {
  let currentAnalyze = analyzeResult.find(a => a.id === currentRegion.id);
  if (currentAnalyze === undefined) {
    return [];
  }
  let attribute = [];
  if (currentAnalyze.gender !== undefined) {
    attribute.push({
      en: LABELS[currentAnalyze.gender.label]['en-US'],
      type: 'gender',
    });
  }
  if (currentAnalyze.category2 !== undefined) {
    attribute.push({
      en: LABELS[currentAnalyze.category2.label]['en-US'],
      type: 'category',
    });
  }
  if (currentAnalyze.category3 !== undefined) {
    attribute.push({
      en: LABELS[currentAnalyze.category3.label]['en-US'],
      type: 'sub_category',
    });
  }
  attribute = [ ...attribute, ...currentAnalyze.attributes ];
  return attribute;
}
</script>

<div id="detect-result" class="primary">
  <div class="cropper-view">
    <div class="image-view" id="image_view" style="">
      {#if image_url}
      <img src={image_url} class="thumbnail-view" id="thumbnail_view" alt="">
      {/if}
      {#if current_region}
      <div id="crop-area" style="
        top: {current_region.ry1 * 100}%;
        left: {current_region.rx1 * 100}%;
        right: {(1 - current_region.rx2) * 100}%;
        bottom: {(1 - current_region.ry2) * 100}%;
      ">
        <div class="dimmer-top"></div>
        <div class="dimmer-right"></div>
        <div class="dimmer-bottom"></div>
        <div class="dimmer-left"></div>
      </div>
      {/if}
    </div>
  </div>
  <div class="results" id="results">
    {#if regions}
    <div id="results_detection">
      <div class="label">ITEMS</div>
      <div id="detection_list">
        {#each regions as region}
        <div id={get_element_id(region)} class="item" class:select={region.id === current_region.id}>
          <a href="#" on:click|preventDefault={change_region(region)}>
            {getCategoryName(region)}
            <b>{parseInt(100 * (region.category1 || region).score)}%</b>
          </a>
        </div>
        {/each}
      </div>
    </div>
    {/if}

    {#await analyze then analyze}
    {#if current_region && analyze && analyze.length > 0}
    <div id="attribute_list">
      <div class="divider"></div>
      <div class="label">ATTRIBUTES</div>
      <ul>
        {#each getCurrentAttributes(current_region, analyze) as attr}
        <li>
          <div class="value">#{attr.en}</div>
          <div class="type">{attr.type}</div>
        </li>
        {/each}
      </ul>
    </div>
    {/if}
    {/await}
  </div>
</div>
