<script>
import { goto } from '@sveltech/routify';
import Header from '../components/Header.svelte';
import Setting from '../components/Setting.svelte';
import ErrorMessage from '../components/ErrorMessage.svelte';
import PXL from '../utils/PXL.js';

let apiBase = localStorage.getItem('apiBase') || 'https://api.pxl.ai/v2';
let apikey = localStorage.getItem('apikey') || '';
let demoImages = localStorage.getItem('demo-images') || '';
const pxl = new PXL(apiBase, apikey);
$: pxl.set(apiBase, apikey);

let parsed = new URL(window.location.href);
let base = parsed.searchParams.get('base');
let key = parsed.searchParams.get('key');
if (base !== null && key !== null) {
  apiBase = base;
  apikey = key;
  localStorage.setItem('apiBase', apiBase)
  localStorage.setItem('apikey', apikey)
  $goto(parsed.origin);
}
let redirect = parsed.searchParams.get('redirect');
if (redirect !== null) {
  $goto(decodeURIComponent(redirect));
}
</script>

<Header />
<slot scoped={{ demoImages, pxl }}></slot>
<Setting bind:apiBase={apiBase} bind:apikey={apikey} bind:demoImages={demoImages} />
<ErrorMessage />
